import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbvue/lib/css/mdb.min.css";
import Vue from "vue";
import App from "./App";
import router from "./router";
import { BootstrapVue, IconsPlugin } from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import "../semantic/dist/semantic.min.css";
import SuiVue from "semantic-ui-vue";
import store from "./store";
import vueResource from "vue-resource";
import VueToast from "vue-toast-notification";
import "vue-toast-notification/dist/theme-default.css";
import Editor from "vue-editor-js";
// Require Froala Editor js file.
require('froala-editor/js/froala_editor.pkgd.min.js')

// Require Froala Editor css files.
require('froala-editor/css/froala_editor.pkgd.min.css')
require('froala-editor/css/froala_style.min.css')

// Import and use Vue Froala lib.
import VueFroala from 'vue-froala-wysiwyg'
Vue.use(VueFroala)


Vue.config.productionTip = false;

Vue.use(Editor);
Vue.use(VueToast);
Vue.use(vueResource);
Vue.use(SuiVue);
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);

Vue.http.options.root = "/api/";


Vue.filter("formatDate", (value) => {
    const d = new Date(value);
    const ye = new Intl.DateTimeFormat("tr", { year: "numeric" }).format(d);
    const mo = new Intl.DateTimeFormat("tr", { month: "short" }).format(d);
    const da = new Intl.DateTimeFormat("tr", { day: "2-digit" }).format(d);
    const hour = new Intl.DateTimeFormat("tr", { hour: "numeric" }).format(d);
    const minute = new Intl.DateTimeFormat("tr", { minute: "numeric" }).format(d);

    return `${da}-${mo}-${ye} / ${hour}: ${minute}`;
});

Vue.filter("parseHtmlToText", text => {
    var tmp = document.createElement("div")
    tmp.innerHTML = text
    return tmp.textContent || tmp.innerText || ""
})

Vue.filter("truncate", (text, number) => {
    let truncatedText = text.substr(0, number)
    return truncatedText + "..."
})


new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount("#app");