<template>
  <div class="blog">
    <div class="blog-header">
      <div class="blur d-flex align-items-center justify-content-center">
        <h1 class="text-white">BLOG</h1>
      </div>
    </div>
    <div class="container py-5">

      <app-blog-card
         v-for="blog in blogPosts"
        :key="blog._id"
        :date="new Date(blog.createdAt)"
        :img="`${baseURL}uploads/blogImages/blog_${blog.blog_img}`"
        :title="blog.title"
        :text="blog.text"
        :rate="blog.rate"
        :comment="blog.comments"
        :id="blog._id"
      ></app-blog-card>

    </div>
  </div>
</template>

<script>
import BlogCard from "../components/BlogCard";
import {mapGetters} from 'vuex'

export default {
  components: {
    appBlogCard: BlogCard,
  },
  computed:{
    ...mapGetters({
      blogPosts:"getBlogPosts",
      baseURL:"getBaseUrl"
    })
  },
  created(){
    this.$store.dispatch('getAllBlogs')
  }
};
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chilanka&family=Noto+Sans&display=swap');

.blog-header {
  height: 500px;
  width: 100%;
  background: url("../assets/blog-post.jpg") center no-repeat;
  background-size: cover;
}
</style>
