<template>
  <div class="add-blog-form">
    <mdb-card>
      <mdb-card-body>
        <span class="exit" @click="toggleAddBlogShow"
          ><i class="fas fa-times"></i
        ></span>
        <form>
          <h1 class="text-center">Blog Düzenleme</h1>

          <div class="blog-list-container row">
            <div class="col-sm-12">
              <app-blogs-list @singleBlog="initUpdateContent"></app-blogs-list>
            </div>
          </div>

          <div class="form-group mt-3">
            <label for="example1">Blog Başlığı</label>
            <input
              v-model="blog.title"
              type="text"
              id="example1"
              class="form-control"
            />
          </div>

          <div class="form-group">
            <label for="">Blog Resim</label>

            <div class="img ">
              <img
                :src="
                  currentImg
                    ? currentImg
                    : 'https://www.mykarateamerica.com/wp-content/uploads/2017/04/default-image-800x600.jpg'
                "
                height="300"
                width="100%"
              />
            </div>

            <div class="input-group pt-3">
              <div class="custom-file">
                <form enctype="multipart/form-data">
                  <input
                    type="file"
                    class="custom-file-input"
                    id="inputGroupFile04"
                    @change="getCurrentDatas($event)"
                  />
                </form>
                <label class="custom-file-label" for="inputGroupFile04"
                  >Resim Seç</label
                >
              </div>
            </div>
          </div>
          <div class="form-group">
            <froala
              :tag="'textarea'"
              :config="config"
              v-model="blog.text"
              class="textarea"
            ></froala>
          </div>
        </form>
        <div class="text-center py-4 mt-3">
          <mdb-btn
            v-if="!isBlogUpdate"
            @click="addBlog(blog), uploadBlogImg(imgForPost), clearInputs()"
            color="warning"
            class="btn-block"
            type="click"
            >Kayıt</mdb-btn
          >
          <mdb-btn
            v-if="isBlogUpdate"
            @click="
              updateBlog(blog),
                uploadBlogImg(imgForPost),
                clearInputs(),
                toggleBlogUpdate()
            "
            color="danger"
            class="btn-block"
            type="click"
            >Güncelle</mdb-btn
          >
          <!-- <app-alert-message
            v-if="emptyControl"
            header="Uyarı !!"
            message="Lütfen bütün alanları doldurunuz"
          ></app-alert-message> -->
        </div>
        <span
          @click="clearInputs(), toggleBlogUpdate(false)"
          class="clear-btn btn btn-danger btn-xs position-absolute"
          ><i class="fas fa-trash"></i
        ></span>
      </mdb-card-body>
    </mdb-card>
  </div>
</template>

<script>
import { mdbBtn, mdbCard, mdbCardBody } from "mdbvue";
import BlogsList from "./BlogsList";
import { mapMutations, mapActions, mapGetters } from "vuex";

// import AlertMessage from "../toolbox/AlertMessage";

export default {
  name: "Basic",
  components: {
    mdbBtn,
    mdbCard,
    mdbCardBody,
    appBlogsList: BlogsList,
  },
  data() {
    return {
      currentImg: "",
      imgForPost: {},
      imgName: "",
      blog: {
        title: "",
        text: "",
        blog_img: this.imgName,
      },
      config: {
        vueIgnoreAttrs: ["textarea", "textarea"],
      },
    };
  },
  methods: {
    initUpdateContent(e) {
      const { blog_img, title, text, _id } = e[0];
      this.blog = {
        blog_img,
        title,
        text,
        _id,
      };

      this.currentImg = `${this.baseURL}/uploads/blogImages/blog_${blog_img}`;
    },
    ...mapMutations({
      toggleAddBlogShow: "toggleAddBlogShow",
      toggleBlogUpdate: "toggleBlogUpdate",
    }),
    ...mapActions({
      addBlog: "addBlog",
      uploadBlogImg: "uploadBlogImg",
      updateBlog: "updateBlog",
    }),

    getCurrentDatas(e) {
      this.currentImg = URL.createObjectURL(e.target.files[0]);

      const formData = new FormData();
      formData.append("blog", e.target.files[0]);
      this.imgForPost = formData;
      this.blog.blog_img = e.target.files[0].name;
    },
    clearInputs() {
      (this.currentImg = ""),
        (this.blog = {
          title: "",
          text: "",
          blog_img: "",
        });
    },
  },
  computed: {
    ...mapGetters({
      isBlogUpdate: "getBlogUpdate",
      baseURL: "getBaseUrl",
    }),
  },
};
</script>

<style scoped>
.clear-btn {
  top: 10px;
  left: 12px;
}
.default {
  border: 3px dashed black;
}
.exit {
  position: absolute;
  right: 25px;
  font-size: 20px;
  cursor: pointer;
}
.add-blog-form {
  width: 90%;
  overflow-y: scroll;
  max-height: 90vh;
}
.img {
  height: 300px;
  width: 100%;
}
.textarea{
  border:2px solid black !important;
}
.img img {
  object-fit: cover;
  border-radius: 15px;
}

/* .text-area{
  height:500px;
} */
</style>
