<template>
  <div class="register-container" :class="{'show' : showRegister}">
      <span class="exit" @click="toggleShow"
      ><i class="fas fa-times text-white"></i
    ></span>
    <app-register-form></app-register-form>
  </div>
</template>

<script>
import RegisterForm from "./RegisterForm";
import {mapMutations , mapGetters} from 'vuex'

export default {
  components: {
    appRegisterForm: RegisterForm,
  },
  computed:{
      ...mapGetters({
          showRegister : 'getShowRegister'
      })
  },
  methods:{
      ...mapMutations({
          toggleShow : 'toggleShowRegister'
      })
  }
};
</script>

<style scoped>
.register-container {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index:-1;
  opacity: 0;
  transition:0.5s ease-in;
}
.exit{
  position: absolute;
  right:50px;
  top:50px;
  font-size:50px;
  cursor:pointer;
}
.show {
  opacity: 1;
  z-index: 4;
}
</style>
