<template>
  <sui-comment-group>
    <h3 is="sui-header" dividing>Comments</h3>
    <sui-comment :key="comment._id" v-for="comment in singleBlog.comments">
      <!-- Ana Yorum -->
      <sui-comment-avatar
        :src="`${baseURL}/uploads/${comment.user.profile_image}`"
      />
      <sui-comment-content>
        <a is="sui-comment-author">{{ comment.user.name }}</a>
        <sui-comment-metadata>
          <div>{{ new Date(comment.createdAt) | formatDate }}</div>
        </sui-comment-metadata>
        <sui-comment-text>{{ comment.text }}</sui-comment-text>
        <sui-comment-actions>
          <sui-comment-action class="reply" @click="focusTextarea(comment._id)"
            >Cevapla</sui-comment-action
          >
        </sui-comment-actions>
      </sui-comment-content>

      <!-- Alt yorum template -->

      <sui-comment-group>
        <sui-comment
          v-for="subComment in comment.subComments"
          :key="subComment._id"
        >
          <sui-comment-avatar
            :src="`${baseURL}/uploads/${subComment.user.profile_image}`"
          />
          <sui-comment-content>
            <a is="sui-comment-author">{{ subComment.user.username }}</a>
            <sui-comment-metadata>
              <div>{{ subComment.createdAt | formatDate }}</div>
            </sui-comment-metadata>
            <sui-comment-text>{{ subComment.text }}</sui-comment-text>
          </sui-comment-content>
        </sui-comment>
      </sui-comment-group>
    </sui-comment>

    <sui-form class="mt-4" reply>
      <textarea class="textarea" ref="a" v-model="commentText" />
      <sui-button
        v-if="!isAnswer"
        @click.prevent="addComment()"
        class="mt-3"
        content="Yorum Ekle"
        label-position="left"
        icon="edit"
        primary
        :disabled="!isLogin"
      />
      <sui-button
        v-else
        @click.prevent="addComment()"
        class="mt-3"
        content="Yorumu Cevapla"
        label-position="left"
        icon="reply"
        primary
        :disabled="!isLogin"
      />
    </sui-form>
  </sui-comment-group>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "MinimalExample",
  data() {
    return {
      parentId: null,
      isAnswer: false,
      commentText: "",
    };
  },
  props: ["blogId"],
  computed: {
    ...mapGetters({
      signedInUserId: "getSignedInUserId",
      isLogin: "getIsLogin",
      singleBlog: "getSingleBlog",
      baseURL: "getBaseUrl",
    }),
  },
  methods: {
    addComment() {
      let commentData = {
        userId: this.signedInUserId,
        commentText: this.commentText,
        blogId: this.blogId,
        parentId: this.parentId,
      };

      if (this.parentId !== null) {
        this.$store.dispatch("answerComment", commentData);
      } else {
        this.$store.dispatch("addCommentToBlog", commentData);
      }

      this.commentText = null;
    },
    focusTextarea(parentId) {
      this.parentId = parentId;
      this.isAnswer = true;
      this.$refs.a.focus();
      document.documentElement.scrollTop =
        document.documentElement.clientHeight;
    },
  },
  created() {
    window.addEventListener("click", (e) => {
      if (e.target.className !== "textarea" && e.target.className !== "reply") {
        this.parentId = null;
        this.isAnswer = false;
      }
    });
  },
};
</script>
