<template>
  <div class="register-form">
    <mdb-card>
      <mdb-card-body>
        <form>
          <p class="h4 text-center py-4">Kayıt Ol</p>
          <div class="grey-text">
            <mdb-input
              v-model="userRegisterInfo.name"
              label="Your name"
              icon="signature"
              group
              type="text"
              validate
              error="wrong"
              success="right"
            />
            <mdb-input
              v-model="userRegisterInfo.username"
              label="Kullanıcı Adı"
              icon="user"
              group
              type="text"
              validate
              error="wrong"
              success="right"
            />
            <mdb-input
              v-model="userRegisterInfo.email"
              label="E-mail adresi"
              icon="envelope"
              group
              type="email"
              validate
              error="wrong"
              success="right"
            />
            <mdb-input
              v-model="userRegisterInfo.password"
              label="Parola"
              icon="lock"
              group
              type="password"
              validate
            />
            <mdb-input
              v-model="userRegisterInfo.repeatPassword"
              label="Parola Tekrar"
              icon="lock"
              group
              type="password"
              validate
            />
          </div>
        </form>
        <div class="text-center py-4 mt-3">
          <mdb-btn
            :disabled="emptyControl"
            @click="registerUser(userRegisterInfo), clearInputs()"
            color="warning"
            class="btn-block"
            type="click"
            >Kayıt</mdb-btn
          >

          <!-- <app-alert-message
            v-if="emptyControl"
            header="Uyarı !!"
            message="Lütfen bütün alanları doldurunuz"
          ></app-alert-message> -->
        </div>
      </mdb-card-body>
    </mdb-card>
  </div>
</template>

<script>
import { mdbInput, mdbBtn, mdbCard, mdbCardBody } from "mdbvue";
import { mapActions } from "vuex";
// import AlertMessage from "../toolbox/AlertMessage";

export default {
  name: "Basic",
  components: {
    mdbInput,
    mdbBtn,
    mdbCard,
    mdbCardBody,
    // appAlertMessage: AlertMessage,
  },
  data() {
    return {
      userRegisterInfo: {
        name: "",
        username: "",
        email: "",
        password: "",
        repeatPassword: "",
      },
    };
  },
  methods: {
    ...mapActions({
      registerUser: "registerUser",
      validation: "validation",
    }),
    clearInputs() {
      this.userRegisterInfo = {
        name: "",
        username: "",
        email: "",
        password: "",
        repeatPassword: "",
      };
    },
  },
  computed: {
    emptyControl() {
      for (let key in this.userRegisterInfo) {
        if (this.userRegisterInfo[key] === "") return true;
      }

      return false;
    },
  },
};
</script>

<style>
.register-form {
  width: 420px;
}

@media (max-width: 720px) {
  .register-form {
    width: 90%;
    position: absolute;
    bottom: 20px;
  }
}
</style>
