<template>
  <router-link tag="div" :to="singleBlogRoute" class="blog-card mb-5">
    <div class="personal-info d-flex align-items-center">
      <span class="p-img"><img :src="require('@/assets/2.jpg')" alt=""/></span>
      <span class="p-info d-flex flex-column pl-3">
        <strong>Malik Korucu</strong>
        <small>{{ date | formatDate }}</small></span
      >
    </div>
    <div class="blog-img ">
      <img :src="img" alt="" />
    </div>

    <div class="content my-3">
      <h1>{{ title }}</h1>
      <div
        v-html="
          this.$options.filters.parseHtmlToText(text).substr(0, 500) + '...'
        "
      ></div>
    </div>
    <div class="rates">
      <span
        ><small>{{ rate }}</small> <i class="fas fa-heart"></i
      ></span>
      <span
        ><small>{{ comment.length }}</small
        ><i class="far fa-comments"></i
      ></span>
    </div>
  </router-link>
</template>

<script>
export default {
  data() {
    return {
      singleBlogRoute: {
        name: "single-blog",
        params: {
          id: this.id,
        },
      },
    };
  },
  parseHtmlToText(text) {
    var tmp = document.createElement("div");
    tmp.innerHTML = text;
    return tmp.textContent || tmp.innerText || "";
  },
  props: ["date", "img", "title", "text", "rate", "comment", "id"],
};
</script>

<style scoped>
.blog-card {
  width: 100%;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.1);
  border-bottom: 5px solid #ffc000;
  padding: 20px;
  border-radius: 15px;
  position: relative;
  cursor: pointer;
}

.content p {
  font-size: 1.1rem;
}

.p-img img {
  border-radius: 50px;
  height: 50px;
  width: 50px;
}
.content p {
  word-break: break-all;
}
.blog-img {
  margin-top: 20px;
  height: 200px !important;
}
.blog-img img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.rates {
  display: flex;
  justify-content: flex-end;
}
.rates i {
  font-size: 25px;
  margin: 0 5px;
}
</style>
