<template>
  <div>
    <div class="single-blog-header">
      <img height="500" width="100%" :src="`${baseURL}uploads/blogImages/blog_${singleBlog.blog_img}`" alt="">
    </div>
    <div class="container py-5">
      <div class="blog">
        <h1 class="text-center">{{singleBlog.title}}</h1>

        <div v-html="singleBlog.text" class="blog-text">
          {{singleBlog.text}}
        </div>

        <app-comment :blogId="$route.params.id"></app-comment>

      </div>
    </div>
  </div>
</template>

<script>
import Comment from "../components/Comment";
import {mapGetters} from 'vuex'

export default {
  components: {
    appComment: Comment,
  },
  computed:{
    ...mapGetters({
      singleBlog : "getSingleBlog",
      baseURL : "getBaseUrl"
    })
  },
  created(){
    this.$store.dispatch('getSingleBlog' , this.$route.params.id)
    document.documentElement.scrollTop = 0
  }
};
</script>

<style scoped>
.blog {
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.2);
  padding: 50px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.single-blog-header{
  height:500px;
}
.single-blog-header img{
  object-fit:cover;
}
.blog-text {
  letter-spacing: 1px;
  font-size: 20px;
  word-break:break-all;
}
.blog-text img{
  border:2px solid black !important;
}
@media(max-width:720px){
  .blog{
    padding:10px;
  }
}
</style>
