<template>
  <div>
    <div class="menu" :class="{ show: show }">
      <ul>
        <router-link tag="li" :to="{ name: 'about' }"
          ><span @click="toggleRouter">HAKKIMDA</span></router-link
        >
        <router-link tag="li" :to="{ name: 'blog' }"
          ><span @click="toggleRouter">BLOG</span></router-link
        >
        <li><span>İLETİŞİM</span></li>
        <router-link tag="li" :to="{ name: 'portfolio' }"
          ><span @click="toggleRouter">PORTFOLIO</span></router-link
        >
      </ul>
    </div>

    <div @click="show = !show" class="menu-open-button">
      <span  class="exit"
        ><i class="fas fa-times"></i
      ></span>
      <span>
        <span class="d-inline-block py-1 t ">MALİK</span><br />
        <span class="d-inline-block py-2 ">KORUCU</span>
        <small>click here to open</small>
      </span>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      show: true,
      r_show: false,
    };
  },
  props: ["rs"],
  methods: {
    ...mapMutations({
      toggleRouter: "toggleRouterView",
    }),
    sendToParent() {
      if (!this.r_show) {
        this.r_show = !this.r_show;
        this.$emit("rs", this.r_show);
      } else {
        this.r_show = !this.r_show;
        this.$emit("rs", this.r_show);
        setTimeout(() => {
          this.r_show = !this.r_show;
          this.$emit("rs", this.r_show);
        }, 600);
      }
    },
    closeRouter() {
      alert("aslkşfjdşlas");
    },
  },
};
</script>

<style scoped>
.menu {
  width: 200px;
  position: absolute;
  bottom: 280px;
  left: 100px;
  transform: translateY(150%);
  transition: 1s;
  background: transparent;
  height: 100px;

}
.menu-open-button {
  background: #ffc000;
  box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.5);
  height: 140px;
  width: 250px;
  position: absolute;
  left: 100px;
  bottom: 100px;
  color: white;
  font-size: 25px;
  padding: 25px 15px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 30px;
}

.menu-open-button small {
  display: block;
  position: absolute;
  font-size: 12px;
  bottom: 5px;
  right: 10px;
}
.show {
  transform: translateY(-60%);
  visibility: visible;
}
.exit {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
}
.exit i:hover {
  color: black;
}
ul {
  list-style-type: none;
  padding: 0;
}
ul li {
  width: 100%;
  color: white;
}
ul li span {
  background: black;
  padding: 8px;
  font-size: 25px;
  margin: 3px 0;
  display: inline-block;
  opacity: 0;
  transition: 0.5s ease-in opacity;
  cursor: pointer;
}
ul li span:hover {
  background: #ffc000;
}
.menu.show ul li span {
  opacity: 1;
  transition: 0.5s ease-in opacity;
}

@media (max-width: 960px) {
  .menu {
    left: 20px;
  }
  .menu-open-button {
    bottom: 100px;
    left: 20px;
  }
}

@media (max-width: 720px) {
  .menu {
    transform: translateX(-50%);
    left: 44%;
    bottom: 150px;
  }
  .menu-open-button {
    top: 180px;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (max-width: 500px) {
  .menu-open-button {
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>
