<template>
  <div class="login-container" :class="{ show: show }">
    <app-loader></app-loader>
    <span class="exit" @click="toggleShow"
      ><i class="fas fa-times text-white"></i
    ></span>
    <app-login-form> </app-login-form>
  </div>
</template>

<script>
import LoginForm from "./LoginForm";
import { mapMutations } from "vuex";
import Loader from '../common/Loader'

export default {
  components: {
    appLoginForm: LoginForm,
    appLoader:Loader
  },
  computed: {
    show() {
      return this.$store.getters.getShowLogin;
    },
  },
  methods: {
    ...mapMutations({
      toggleShow: "toggleShowLogin",
    }),
  },
};
</script>

<style scoped>
.exit {
  position: absolute;
  right: 50px;
  top: 50px;
  font-size: 50px;
  cursor: pointer;
}
.login-container {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  opacity: 0;
  transition: 0.5s ease-in;
}

.show {
  opacity: 1;
  z-index: 5;
}
</style>
