<template>
  <div class="loader-container" :class="{'show' : isLoaderShow}">
    <div class="spinner-grow text-warning" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'

export default {
    computed:{
        ...mapGetters({
            isLoaderShow:"getIsLoaderShow"
        })
    },
    methods:{
        ...mapMutations({
            toggleIsLoaderShow: "toggleIsLoaderShow"
        })
    }
};
</script>

<style scoped>
.loader-container {
  position:sticky;
  position: absolute;
  left: 0;
  top: 0;
  right:0;
  z-index: 15;
  background: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  height:100%;
  display:none;
}
.show{
    display: flex;
}
</style>
