import Vue from "vue";
import Vuex from "vuex";
import router from "./router";
import auth from "./store/modules/auth";
import alert from "./store/modules/alert";
import blog from "./store/modules/blog";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        showLogin: false,
        showRegister: false,
        showRouterView: false,
        blogUpdate: false,
        name: "malik korucu",
        fullScreen: false,
        currentMenu: "",
        isLogin: false,
        addBlogShow: false,
        signedInUser: {},
        baseURL: "http://localhost:5000/",
        isLoaderShow: false,
    },
    getters: {
        getShowLogin(state) {
            return state.showLogin;
        },
        getName(state) {
            return state.name;
        },
        getShowRouterView(state) {
            return state.showRouterView;
        },
        getFullScreen(state) {
            return state.fullScreen;
        },
        getShowRegister(state) {
            return state.showRegister;
        },
        getIsLogin(state) {
            return state.isLogin;
        },
        getSignedInUser(state) {
            return state.signedInUser;
        },
        getAddBlogShow(state) {
            return state.addBlogShow;
        },
        getBlogUpdate(state) {
            return state.blogUpdate;
        },
        getBaseUrl(state) {
            return state.baseURL;
        },
        getIsLoaderShow(state) {
            return state.isLoaderShow;
        },
        getSignedInUserId(state) {
            return state.signedInUser._id;
        },
    },
    mutations: {
        toggleShowLogin(state) {
            state.showLogin = !state.showLogin;
        },
        toggleShowRegister(state) {
            state.showRegister = !state.showRegister;
        },
        toggleRouterView(state) {
            if (!state.showRouterView) {
                state.showRouterView = !state.showRouterView;

            } else {
                state.showRouterView = !state.showRouterView;
                setTimeout(() => {
                    state.showRouterView = !state.showRouterView;
                }, 800);
            }
        },
        clearSignedInUser(state) {
            state.signedInUser = {};
        },
        toggleIsLogin(state) {
            state.isLogin = !state.isLogin;
        },
        setCurrentMenu(state, value) {
            state.currentMenu = value;
        },
        toggleFullScreen(state) {
            state.fullScreen = !state.fullScreen;
        },
        closeRoute(state) {
            state.showRouterView = false;
            router.replace({ name: "main" });
            state.fullScreen = false;
        },
        setSignedInUser(state, user) {
            state.signedInUser = user;
        },
        openIsLogin(state) {
            state.isLogin = true;
        },
        toggleAddBlogShow(state) {
            state.addBlogShow = !state.addBlogShow;
            state.showRouterView = false

            if (router.app._route.name !== "blog-edit") {
                router.replace({ name: "blog-edit" });
                state.addBlogShow = "true"
            } else {
                router.replace({ name: "main" })
                state.addBlogShow = false
            }
        },
        toggleBlogUpdate(state, booleanValue) {
            state.blogUpdate = booleanValue;
        },
        toggleIsLoaderShow(state) {
            state.isLoaderShow = !state.isLoaderShow;
        },
    },
    actions: {},
    modules: {
        auth,
        alert,
        blog,
    },
});