import Vue from "vue";
// import router from "../../router";
// import router from '../../router'

const state = {};
const getters = {};
const mutations = {};
const actions = {
    registerUser({ dispatch, commit }, user) {
        commit("toggleIsLoaderShow")
        Vue.http
            .post("auth/register", user)
            .then((res) => {
                dispatch("alert", {
                    message: res.data.message,
                    type: "success",
                    duration: 3000,
                });
                commit("toggleShowRegister");
                commit("toggleShowLogin");
                commit("toggleIsLoaderShow")
            })
            .catch((err) => {
                console.log(err);
                dispatch("alert", {
                    message: err.data.message,
                    type: "error",
                    duration: 3000,
                });
            });
    },

    signInUser({ commit, dispatch }, user) {
        commit("toggleIsLoaderShow")
        Vue.http
            .post("auth/login", user)
            .then((res) => {
                localStorage.setItem(
                    "authorization",
                    JSON.stringify({...res.body.data, ...res.body.data.user })
                );

                commit("setSignedInUser", {...res.body.data.user });

                dispatch("alert", {
                    message: `Hoşgeldin ${res.body.data.user.name}`,
                    type: "default",
                    duration: 3000,
                });

                const expires_in = JSON.parse(localStorage.getItem("authorization"))
                    .expiresIn;
                dispatch("logTimer", expires_in);

                commit("toggleIsLoaderShow")
                commit("toggleShowLogin");
                commit("toggleIsLogin");
            })
            .catch((err) => {
                dispatch("alert", {
                    message: err.data.message,
                    type: "warning",
                    duration: 3000,
                });
            });
    },

    signOut({ commit, dispatch }) {
        localStorage.removeItem("authorization");
        commit("clearSignedInUser");
        commit("toggleIsLogin");
        dispatch("alert", {
            message: "Başarıyla çıkış yaptınız...",
            type: "success",
            duration: 3000,
        });
    },

    initAuth({ dispatch, commit }) {
        let loggedInUser = JSON.parse(localStorage.getItem("authorization"));

        if (loggedInUser) {
            let expireDate = new Date(loggedInUser.expireDate).getTime();
            let user = JSON.parse(localStorage.getItem("authorization"));

            commit("setSignedInUser", user);
            commit("toggleIsLogin");

            if (new Date().getTime() >= expireDate) {
                dispatch("signOut");
            } else {
                let leftTime = expireDate - new Date().getTime();
                dispatch("logTimer", leftTime);
            }
        }
    },
    logTimer({ dispatch }, expire) {
        setTimeout(() => {
            dispatch("signOut");
        }, expire);
    },
    uploadImage(vueContext, formData) {
        const id = vueContext.getters.getSignedInUser._id;
        Vue.http
            .post("auth/upload", formData, {
                headers: {
                    id: id,
                },
            })
            .then((res) => {
                let signedInUser = vueContext.getters.getSignedInUser;
                const img = res.body.data.profile_image;
                let updatedUser = {
                    ...signedInUser,
                    profile_image: img,
                };

                let beforeStorage = JSON.parse(localStorage.getItem("authorization"));

                localStorage.setItem(
                    "authorization",
                    JSON.stringify({
                        ...beforeStorage,
                        profile_image: updatedUser.profile_image,
                    })
                );
                vueContext.commit("setSignedInUser", updatedUser);
            })
            .catch((err) => console.log(err));
    },
};

export default {
    state,
    getters,
    mutations,
    actions,
};