<template>
  <sui-table celled>
    <sui-table-header>
      <sui-table-row>
        <sui-table-header-cell>Blog Id</sui-table-header-cell>
        <sui-table-header-cell>Blog Başlığı</sui-table-header-cell>
        <sui-table-header-cell>Oluşturulma Tarihi</sui-table-header-cell>
        <sui-table-header-cell>Blog İçerik</sui-table-header-cell>
        <sui-table-header-cell>Blog Edit</sui-table-header-cell>
      </sui-table-row>
    </sui-table-header>
    <sui-table-body>
      <sui-table-row :key="blog._id" v-for="blog in blogPosts">
        <sui-table-cell> {{ blog._id }}</sui-table-cell>
        <sui-table-cell>{{ blog.title }}</sui-table-cell>
        <sui-table-cell>{{ new Date(blog.createdAt) | formatDate }}</sui-table-cell>
        <sui-table-cell class="text">
          {{ blog.text | parseHtmlToText | truncate(100) }}</sui-table-cell
        >
        <sui-table-cell class="d-flex"
          ><span @click="removeBlog(blog._id)" class="btn btn-danger">Sil</span>
          <span @click.prevent="getBlog(blog._id)" class="btn  btn-warning"
            >Düzenle</span
          >
        </sui-table-cell>
      </sui-table-row>
    </sui-table-body>
  </sui-table>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "TableRatingExample",
  computed: {
    ...mapGetters({
      blogPosts: "getBlogPosts",
    }),
  },
  methods: {
    ...mapActions({
      removeBlog: "removeBlog",
    }),
    getBlog(id) {
      this.$store.commit("toggleBlogUpdate", true);
      let singleBlog = this.$store.getters.getSingleBlog_filter(id);
      this.$store.commit("setSingleBlog", singleBlog);
      this.$emit("singleBlog", singleBlog);
    },
  },
};
</script>

<style scoped>
.text {
  word-break: break-all !important;
}
</style>
