<template>
  <span class="a">
    <input
      @change="setProfileImg($event)"
      :style="{ display: 'none' }"
      type="file"
      ref="file"
    />
    <sui-dropdown
      size="massive"
      color="yellow"
      :text="user.username"
      button
      floating
      class="btn btn-warning font-italic"
    >
      <sui-dropdown-menu>
        <sui-dropdown-item @click="toggleAddBlogShow" v-if="user.role == 'admin'"
          ><sui-icon name="edit" />Blogları Düzenle</sui-dropdown-item
        >
        <sui-dropdown-item v-if="user.role == 'admin'"
          ><sui-icon name="edit" />Portfolio Düzenle</sui-dropdown-item
        >
        <sui-dropdown-item
          v-if="user.role == 'user'"
          @click="$refs.file.click()"
          >Profil Fotoğrafı Ekle</sui-dropdown-item
        >
        <sui-dropdown-item @click="signOut"
          ><i class="sign out alternate icon"></i>Çıkış</sui-dropdown-item
        >
      </sui-dropdown-menu>
    </sui-dropdown>
  </span>
</template>

<script>
import { mapActions, mapGetters,mapMutations } from "vuex";


export default {
  computed: {
    ...mapGetters({
      user: "getSignedInUser",
    }),
  },
  methods: {
    ...mapActions({
      signOut: "signOut",
    }),
    ...mapMutations({
      toggleAddBlogShow : "toggleAddBlogShow"
    }),
    setProfileImg(e) {
      let img = e.target.files[0];
      const formData = new FormData();
      formData.append("avatar", img);

      this.$store.dispatch("uploadImage", formData);
    },
  },
};
</script>
<style scoped></style>
