import Vue from "vue";
// import VueRouter from "vue-router";

const state = {
    blogPosts: [],
    singleBlog: {},
    comments: [],
};

const getters = {
    getBlogPosts(state) {
        return state.blogPosts;
    },
    getSingleBlog_filter(state) {
        return (id) => state.blogPosts.filter((blog) => blog._id === id);
    },
    getSingleBlog(state) {
        return state.singleBlog;
    },
};

const mutations = {
    setBlogPosts(state, blogs) {
        state.blogPosts = blogs;
    },
    addBlog(state, blog) {
        state.blogPosts.push(blog);
    },
    setSingleBlog(state, blog) {
        state.singleBlog = blog;
    },
    // updateSingleBlog(state, blog) {
    //     let a = state.singleBlog.filter(b => b._id == blog._id)[0]

    // }
};

const actions = {
    addBlog({ commit }, blog) {
        Vue.http
            .post("admin/addBlog", blog)
            .then((res) => {
                commit("addBlog", res.body.data);
            })
            .catch((err) => console.log(err));
    },
    uploadBlogImg(vueContext, formData) {
        Vue.http
            .post("admin/uploadBlogImg", formData)
            .then((res) => {
                vueContext.dispatch("alert", {
                    message: res.data.message,
                    type: "success",
                    duration: 3000,
                });
            })
            .catch((err) => console.log(err));
    },
    getAllBlogs({ commit }) {
        Vue.http
            .get("blog/getAllBlogs")
            .then((res) => {
                commit("setBlogPosts", res.body.data);
            })
            .catch((err) => console.log(err));
    },
    updateBlog({ dispatch }, blog) {
        Vue.http
            .post("blog/updateBlog/" + blog._id, blog)
            .then((res) => {
                dispatch("alert", {
                    message: res.data.message,
                    type: "success",
                    duration: 3000,
                });
                dispatch("getAllBlogs");
            })
            .catch((err) => console.log(err));
    },
    removeBlog(vueContext, id) {
        if (confirm("blog postunu silmek istediginden emin misin ?")) {
            Vue.http
                .delete("blog/deleteBlog/" + id)
                .then((res) => {
                    console.log(res);
                    vueContext.dispatch("getAllBlogs");
                })
                .catch((err) => console.log(err));
        }
    },
    getSingleBlog({ commit }, id) {
        commit("toggleIsLoaderShow")
        Vue.http
            .get("blog/singleBlog/" + id)
            .then((res) => {
                console.log(res)
                commit("setSingleBlog", res.body.data);
                commit("toggleIsLoaderShow")
            })
            .catch((err) => console.log(err));
    },

    addCommentToBlog(vueContext, commentData) {
        vueContext.commit('toggleIsLoaderShow')
        const { blogId } = commentData;
        Vue.http
            .post("blog/addComment/" + blogId, commentData)
            .then((res) => {
                vueContext.commit('toggleIsLoaderShow')
                console.log(res);
                vueContext.dispatch("getSingleBlog", blogId);
            })
            .catch((err) => console.log(err));
    },
    answerComment(vueContext, commentData) {
        // blog/answerComment/id
        vueContext.commit('toggleIsLoaderShow')
        Vue.http
            .post("blog/answerComment/" + commentData.parentId, commentData)
            .then((res) => {
                vueContext.commit('toggleIsLoaderShow')
                vueContext.dispatch('getSingleBlog', res.body.data.blog)
            })
            .catch((err) => console.log(err));

        // console.log(commentData)
    },
};

export default {
    state,
    getters,
    mutations,
    actions,
};