<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  components: {},

  name: "App",
  created() {
    this.$store.dispatch("initAuth");
    this.$store.dispatch("getAllBlogs");

    if (this.$route.name !== "main") {
      this.$store.commit("toggleRouterView");
    }


    if(this.$route.name === "blog-edit"){
      this.$store.commit("toggleAddBlogShow")
    }
  },
};
</script>

