@import url("https://fonts.googleapis.com/css2?family=Domine:wght@400;700&family=Merriweather&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@400;700&family=Merriweather&family=PT+Sans+Caption&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@400;700&family=Merriweather&family=PT+Sans+Caption&family=Roboto&display=swap");
html,
body {
  overflow-x: hidden;
}
body {
  position: relative;
}
* {
  box-sizing: border-box;
}

.animate__animated {
  animation-duration: 0.5s;
}
</style>
