<template>
  <div class="login-form">
    <mdb-card>
      <mdb-card-body>
        <form>
          <p class="h4 text-center py-4">Giriş Yap</p>
          <div class="grey-text">
            <mdb-input
              v-model="user.username"
              label="Kullanıcı Adı"
              icon="user"
              group
              type="text"
              validate
              error="wrong"
              success="right"
            />

            <mdb-input
              v-model="user.password"
              label="Parola"
              icon="lock"
              group
              type="password"
              validate
            />
          </div>
        </form>
        <div class="text-center py-4 mt-3">
          <mdb-btn
            @click="signInUser(user), clearInputs()"
            color="warning"
            class="btn-block"
            type="click"
            >Giriş</mdb-btn
          >
        </div>
      </mdb-card-body>
    </mdb-card>
  </div>
</template>
<script>
import { mdbInput, mdbBtn, mdbCard, mdbCardBody } from "mdbvue";
import { mapActions } from "vuex";

export default {
  name: "Basic",
  components: {
    mdbInput,
    mdbBtn,
    mdbCard,
    mdbCardBody,
  },
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    ...mapActions({
      signInUser: "signInUser",
    }),
    clearInputs() {
      this.user = {
        username: "",
        password: "",
      };
    },
  },
};
</script>

<style>
.login-form {
  width: 420px;
}

@media (max-width: 720px) {
  .login-form {
    left: auto;
    width: 90%;
    bottom: 20px;
  }
}
</style>
